.education {
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: rgb(177, 189, 255);
  background: linear-gradient(
    0deg,
    rgba(177, 189, 255, 0.7769958325126926) 0%,
    rgba(0, 212, 255, 0) 100%
  );
}
#edu-id {
  text-align: center;
  font-weight: bold;
  font-size: 18px;
}
@media screen and (min-width: 600px) {
  .edu {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-evenly;
    align-items: flex-end;
    height: 480px;
  }
  .edu img {
    height: 100px;
    overflow: hidden;
    margin-left: auto;
    margin-right: auto;
  }
  .edu h2 {
    font-weight: bold;
  }
  .edu-1,
  .edu-2,
  .edu-3 {
    width: 250px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin: 5% 5% 0 5%;
    padding: 20px;
    border-radius: 10px 10px 0 0;
    box-shadow: 0px 0px 2px gray;
  }
  .edu-1 {
    height: 90%;
  }
  .edu-2 {
    height: 70%;
  }
  .edu-3 {
    height: 50%;
  }
}
@media screen and (max-width: 599px) {
  .edu {
    display: flex;
    flex-direction: column;
  }
  .edu-1,
  .edu-2,
  .edu-3 {
    display: flex;
    flex-direction: row;
    align-items: center;
    border: 1px solid rgba(128, 128, 128, 0.445);
    box-shadow: 0px 0px 5px gray;
    border-radius: 10px;
    padding: 2%;
    margin: 3%;
  }
  .edu img {
    height: 100px;
    padding: 2%;
  }
  .edu h2 {
    font-weight: bold;
  }
}
