.NavbarforSS {
  position: fixed;
  z-index: 2;
  top: 50px;
  right: 0;
  height: fit-content;
  padding: 15%;
  text-align: center;
  background-color: rgba(240, 248, 255, 0.232);
  backdrop-filter: blur(10px);
  animation: navAnime linear 0.4s both;
}
@keyframes navAnime {
  from {
    transform: translate(150px);
  }
  to {
    transform: translate();
  }
}
.NavbarforSS ul li {
  padding: 5px;
}
@media screen and (min-width: 600px) {
  .NavbarforSS {
    visibility: hidden;
  }
}
