.main {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 10% 10% 0% 10%;
  background: rgb(177, 243, 255);
  background: linear-gradient(
    0deg,
    rgba(177, 243, 255, 1) 0%,
    rgba(0, 212, 255, 0) 100%
